import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  current_date = '';
  constructor() {
    const date = new Date();
    this.current_date = '' + date.getDate() + '/' + date.getMonth() + '/' + date.getFullYear();
  }
}
